/*dws*/
.dws {
	 position: absolute;
	 top: 93%;
	 left: 50%;
   display: flex;
	transform: translate(-50%, -50%);
	
  }
  .butt {
	position: absolute;
	transform: translate(-50%, -50%);
	border: 2px solid #218294;
	font-size: 12px;
	color: #218294;
	text-decoration: none;
	text-transform: uppercase;
	width: 170px;
	height: 35px;
	display: flex;
	align-items: center;
	justify-content: center;
	line-height: 15px;
	font-family: Arial, sans-serif;
	position: relative;
	transition: .5s;
	overflow: hidden;
	border-radius: 10px;
  }
  .butt::before,
  .butt::after {
	 position: absolute;
	content: '';
	width: 100%;
	height: 100%;
	background: #218294;
	top: 0;
	left: -100%;
	opacity: .5;
	transition: .3s;
	z-index: -1;
  }
  .butt::after {
	opacity: 1;
	transition-delay: .2s;
  }
  .butt:hover {
	color: #fff;
  }
  .butt:hover::before,
  .butt:hover::after {
	left: 0;
  }
  
  
